html,
body,
#root {
    height: 100%;
}

* {
    transition: 200ms all ease-in;
}

@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    // width: 1200px;
    flex-direction: column;

    .row {
        width: 100%;

        @include flex-center;
    }

    height: 100%;
}
img{
    height: 200px;
    width: 200px;
    margin-top:20px;
}

form {
    //   display: flex;
    @include flex-center;
    flex-direction: column;

    input {
        width: 100%;
        display: block;
        width: 600px;
        border: 1px solid pink;
        padding: 20px 10px;
        border-radius: 40px;
    }

    button {
        border: 1px solid pink;
        // padding: 20px 10px;
        border-radius: 40px;
        width: 200px;
        margin-top: 2rem;
    }
}